import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"

import styles from './style.module.scss';

interface State {
  action: string
  data: string
  uniqueId: string
  options: {
    layer: string,
    units: string
  }
  isFocused: boolean
  isChecked: boolean
}

declare global {
  interface Window {
    chrome: any;
  }
}

class SendGeometry extends StreamlitComponentBase<State> {
  public state = {
      action: '',
      data: '',
      uniqueId: '',
      options: {
        layer: '',
        units: ''
      },
      isFocused: false, 
      isChecked: false
    }

  public render = (): ReactNode => {
    return (
      <label className={styles.poCheckboxLabel}>
        <input 
          className={styles.poCheckbox}
          type="checkbox" 
          defaultChecked={this.props.args["isChecked"]} 
          onChange={this.onChange} 
          disabled={this.props.disabled}
          onFocus={this._onFocus}
          onBlur={this._onBlur}/>
        <span className={styles.poCheckboxSpan}>
          Draw Geometry
        </span>
      </label>
    )
  }

  private onChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
    if (typeof window.parent.chrome != "undefined") {
      if (typeof window.parent.chrome.webview == "undefined")
      {
        console.log('[POLLINATION-DEBUG]: webview not found.');
        return;
      }

      let check = event.target.checked;
      let dotNetAction = (check) ? "DrawGeometry" : "DisableDraw";

      var msgObject = {
        action: dotNetAction,
        data: this.props.args["data"],
        uniqueId: this.props.args["uniqueId"],
        options: this.props.args["options"]
      };

      var json = JSON.stringify(msgObject);
      window.parent.chrome.webview.postMessage(json);

      this.setState(
        (prevState) => ({ 
          ...prevState, 
          action: msgObject.action,
          data: msgObject.data,
          uniqueId: msgObject.uniqueId,
          options: msgObject.options,
          isChecked: check
        }),
        () => Streamlit.setComponentValue(msgObject.data)
      );
      console.log('[POLLINATION-DEBUG]: JSON string sent.');
    }
    else
    {
      console.log('[POLLINATION-DEBUG]: chrome not found.');
    }
  }

  private _onFocus = (): void => {
    this.setState({ isFocused: true })
  }

  private _onBlur = (): void => {
    this.setState({ isFocused: false })
  }
}

export default withStreamlitConnection(SendGeometry)
